.alert {
  position: absolute;
  width: 85%;
  height: 100%;
  opacity: 0.9;
}

.alert-box {
  position: absolute;
  width: 15%;
  right: 42.5%;
  top: 38%;
  background-color: #7d9267;
  color: white;
}

.msg {
  background-color: #7d9267;
}

.buttons {
  width: 20%;
  margin: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin-bottom: 1rem;
}

p {
  color: white;
}
.secondImg {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 33rem;
}
.button {
  outline: none;
  border: 0;
  background-color: #7d9267;
  color: white;
  padding: 0.75rem;
  border-radius: 50%;
}

img {
  width: 20rem;
  height: 20rem;
}
.text-area {
  border: 0.05rem #7d9267 solid;
  background-color: #7d9267;
  border-radius: 5%;
  resize: none;
  outline: none;
}
.button,
.text-area {
  margin-top: 1rem;
  color: white;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



